<template>
  <el-card shadow="always">
    <Headline></Headline>
    <div style="clear: both">
      <el-form :inline="true" ref="roomSelectForm" :model="roomSelectForm" :rules="selectRoomRules" label-width="80px">
        <el-form-item label="" prop="roomNumber">
          <el-input v-model.number="roomSelectForm.roomNumber" placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-select v-model="roomSelectForm.type" clearable multiple>
            <el-option v-for="type in roomTypes" :key="type.roomType" :label="type.roomType" :value="type.roomType">
              <span style="float: left">{{ type.roomType }}</span>
              <span style="float: right; color: #8492a6; font-size: 13px">{{''+ type.bedCount }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-select v-model="roomSelectForm.status" clearable multiple>
            <el-option v-for="status in roomStatus" :key="status.value" :label="status.label"
                       :value="status.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" style="clear: left">
          <el-col :span="11">
            <el-form-item prop="minPrice" style="margin-right: 0px">
              <el-input v-model.number="roomSelectForm.minPrice" placeholder="" clearable></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="2">-</el-col>
          <el-col :span="11">
            <el-form-item prop="maxPrice" style="margin-right: 0px">
              <el-input v-model.number="roomSelectForm.maxPrice" placeholder="" clearable></el-input>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item style="float: right;margin-right: 100px">
          <el-button type="primary" @click="selectRoom"></el-button>
          <el-button @click="resetForm('roomSelectForm')"></el-button>
          <el-button type="primary" @click="showBookDialog=true"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <el-dialog title="" :visible.sync="showBookDialog" center :before-close="closeBookDialog">
      <el-form :model="bookForm.order" :rules="bookFormRules" ref="bookForm" label-width="80px" style="height: 300px">
        <el-form-item label="">
          <el-select v-model="bookForm.type" clearable>
            <el-option v-for="type in roomTypes" :key="type.roomType" :label="type.roomType" :value="type.roomType">
              <span style="float: left">{{ type.roomType }}</span>
              <span style="float: right; color: #8492a6; font-size: 13px">{{''+ type.bedCount }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="realName">
          <el-input v-model="bookForm.order.realName" style="width: 220px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="idCard">
          <el-input v-model="bookForm.order.idCard" style="width: 220px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="emailAddress">
          <el-input v-model="bookForm.order.emailAddress" style="width: 220px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="date">
          <el-date-picker
            v-model="bookForm.order.date"
            type="daterange"
            start-placeholder=""
            end-placeholder=""
            :default-time="['12:00:00', '12:00:00']"
            :picker-options="checkInPickerOptions"
          ></el-date-picker>
        </el-form-item>
        <!--<el-form-item label="" prop="inform">-->
          <!--<el-checkbox v-model="bookForm.order.inform"></el-checkbox>-->
        <!--</el-form-item>-->
      </el-form>
      <div slot="footer">
        <el-button type="primary" @click="bookRoom"></el-button>
        <el-button @click="closeBookDialog"></el-button>
      </div>
    </el-dialog>
  </el-card>
</template>

<script>
  import Headline from '../../common/Headline'
  import global from '../../common/GlobalVariable'

  export default {
    name: "RoomSelectCriteria",
    components: {Headline},
    props: {
      selectLoading: {
        type: Boolean,
        default: false
      },
      types: {
        type: Array,
        default: []
      }
    },
    data() {
      const checkNumber = (rule, value, callback) => {
        if (!Number.isInteger(value)) {
          callback(new Error(''))
        } else if (value < 0) {
          callback(new Error('0'))
        }
        callback();
      };
      var checkMaxPrice = (rule, value, callback) => {
        setTimeout(() => {
          if (!Number.isInteger(value)) {
            callback(new Error(''));
          } else {
            if (value < this.roomSelectForm.minPrice) {
              callback(new Error(''))
            }
            callback();
          }
        }, 1000);
      };
      return {
        roomTypes: this.types,
        roomStatus: global.roomStatus,
        roomSelectForm: {
          roomNumber: '',
          type: '',
          status: '',
          minPrice: 0,
          maxPrice: 0,
        },
        selectRoomRules: {
          minPrice: [{validator: checkNumber}],
          maxPrice: [{validator: checkMaxPrice}],
        },
        bookForm: {
          type: '',
          order: {
            oid: '',
            realName: '',
            idCard: '',
            date: ['', ''],
            emailAddress: '',
            inform: true,
            rate: 0,
            frontUserName: '',
          }
        },
        bookFormRules: {
          realName: [{validator: global.validateName, trigger: 'blur'}],
          idCard: [{validator: global.validateIdCard, trigger: 'blur'}],
          emailAddress: [{validator: global.validateEmail, trigger: 'blur'}],
        },
        showBookDialog: false,
        checkInPickerOptions:{
          disabledDate(time){
            return time.getTime() < Date.now();
          }
        },
      }
    },
    watch: {
      types: function (val) {
        this.updateTypes(val);
      }
    },
    mounted: function () {
      this.currFrontUser = JSON.parse(localStorage.getItem('currFrontUser'));
    },
    methods: {
      updateTypes: function (val) {
        this.roomTypes = val;
      },
      resetForm: function (name) {
        this.$refs[name].resetFields();
      },
      selectRoom: function () {
        this.$refs['roomSelectForm'].validate((valid) => {
          if (valid) {
            this.$emit('changeLoading', true);
            this.$axios.post('room/select', this.roomSelectForm)
              .then(res => {
                this.$emit('getSpecialRooms', res.data.data);
                this.$emit('changeLoading', false);
              }).catch(error => {
              this.$message.error(error);
              return;
            })
            this.$message('');
          } else {
            return false;
          }
        })
      },
      bookRoom: function () {
        this.$refs['bookForm'].validate((valid) => {
          if (valid) {
            this.bookForm.order.frontUserName = this.currFrontUser.userName;
            this.$axios.post('room/book', this.bookForm)
              .then(res => {
                if (res.data.data === 0) {
                  this.$message.warning('');
                  return;
                }
                this.$message.success('' + res.data.data + ',');
                setTimeout(() => {
                  this.$router.go(0);
                },2000);
              }).catch(error => {
              this.$message.error(error);
              return;
            });
          } else {
            return false;
          }
        })
      },
      closeBookDialog: function () {
        this.showBookDialog = false;
        this.$refs['bookForm'].resetFields();
      },
    }
  }
</script>

<style scoped>

  .el-form-item {
    float: left;
    margin-right: 80px;
  }

</style>



